<template>
  <div
    class="transformation-w large-blur is--template-border"
    v-if="transformation && transformation.desmosId"
  >
    <div class="transformation-form-block w-form">

      <div
        class="tranformation-form is--2"
      >
        <div class="little-row2">
          <a
            @click="$emit('on-close')"
            style="cursor: pointer; width: 1rem; padding: 0.2rem;"
          >
            <i class="fa fa-times"></i>
          </a>
          <h4 class="is--center" style="padding-top: 0.3rem;">Selected Transformation</h4>
        </div>

        <div class="ta-row2 tranformation-form is--2">

          <select
            class="dropdown is--template-border"
            v-show="desmosUrls.length > 1"
            v-model="transformation.desmosId"
          >
            <option
              disabled
              class="dropdown-list w-dropdown-list w--open"
              value=""
            >
              Desoms ID
            </option>
            <option
              class="dropdown-list w-dropdown-list w--open"
              v-for="index in desmosUrls.length || 1"
              :key="index"
              :value="index"
            >
              {{ index }}
            </option>
          </select>
          <img v-show="desmosUrls.length > 1" src="../images/menu-3.svg" loading="lazy" width="25" alt="" class="image-3">
        </div>

        <div class="ta-row2 tranformation-form is--2">
          <select
            class="dropdown is--template-border"
            v-model="transformation.compId"
            @focus="getCompId(transformation.desmosId)"
            @click="setAutoStateAndDetectAttribute(transformation.compId, transformation.desmosId)"
          >
            <option
              disabled
              class="dropdown-list w-dropdown-list w--open"
              value=""
            >
              Component ID
            </option>
            <option
              class="dropdown-list w-dropdown-list w--open"
              v-for="(option, index) in componentIdOption"
              :value="option.id"
              :key="index"
            >
              <ul>
                <li class="latex-render w-embed">
                  <label style="font-size: 2px;">{{ option.idx }}- </label>
                  <label>{{ option.latex }}</label>
                </li>
              </ul>
            </option>
            <option
              class="dropdown-list w-dropdown-list w--open"
              value="desmosCanvas"
            >
              Desmos Canvas
            </option>
          </select>
          <!-- <u-search-select-math
            :options="
              componentIdOption.map((op) => {
                return { id: op.id, name: op.latex };
              })
            "
            v-on:selected="
              (value) => {
                transformation.compId = value.id;
              }
            "
            :selectedOption="{
              id: transformation.compId,
              name: transformation.latex,
            }"
            :disabled="false"
            :maxItem="10"
            placeholder="Select or search a component"
          ></u-search-select-math> -->
          <img src="../images/3d.svg" loading="lazy" width="25" alt="" class="image-3">
        </div>


        <div class="ta-row2 tranformation-form is--2">

          <select
            class="dropdown is--template-border"
            :class="[!transformation.compId ? 'is--deselect' : null, !transformation.attribute ? 'is--error': null ]"
            outlined
            v-model="transformation.attribute"
            v-on:change="getAttr(transformation.attribute)"
          >
            <option
              disabled
              class="dropdown-list w-dropdown-list w--open"
              value=""
            >
              Attribute
            </option>
            <option
              class="dropdown-list w-dropdown-list w--open"
              v-for="(option, i) in transformation.attributeOption"
              :value="option"
              :key="i"
            >
              <ul>
                <li>
                  <label>{{ option }}</label>
                </li>
              </ul>
            </option>
          </select>
          <img src="../images/move-2.svg" loading="lazy" width="25" alt="" class="image-3">
        </div>

        <div
          class="ta-row2 tranformation-form is--2"
          v-if="
            transformation.attribute === 'Color' ||
            transformation.attribute === 'Hidden' ||
            transformation.attribute === 'Select' ||
            transformation.attribute === 'showLabel' ||
            transformation.attribute === 'pointActive'
          "
        >
          <select
            class="dropdown is--template-border"
            :class="[!transformation.attribute ? 'is--deselect' : null, !transformation.value ? 'is--error': null]"
            v-model="transformation.value"
          >
            <option
              class="dropdown-list w-dropdown-list w--open"
              value=""
            >
              Transform Option
            </option>
            <option
              class="dropdown-list w-dropdown-list w--open"
              v-for="(option, i) in transformation.transformOption"
              :value="option"
              :key="i"
            >
              <ul>
                <li>
                  <label>{{ option }}</label>
                </li>
              </ul>
            </option>
          </select>
          <img src="../images/key.svg" loading="lazy" width="25" alt="" class="image-3">
        </div>

        <div class="" v-else-if="transformation.attribute === 'SYNC' || transformation.attribute === 'SYNC-Backward'">
          <label for="latex">Latex</label>
          <input
            v-model="transformation.latex"
            class="dropdown"
            style="width: 99%;"
            readonly
          />
          <div class="normal-row2">
            <div>
              <label for="minValue">Slider Min</label>
              <input
                v-model="transformation.sliderBounds.min"
                class="dropdown"
                style="width: 99%;"
                readonly
              />
            </div>
            <div>
              <label for="maxValue">Slider Max</label>
              <input
                v-model="transformation.sliderBounds.max"
                class="dropdown"
                style="width: 99%;"
                readonly
              />
            </div>
          </div>
        </div>
        <div class="" v-else-if="transformation.attribute === 'SYNC-Custom' || transformation.attribute === 'SYNC-C-Backward'">
          <label for="latex">Latex</label>
          <input
            v-model="transformation.latex"
            class="dropdown"
            style="width: 99%;"
            :class="!transformation.latex ? 'is--error': ''"
          />
          <div class="normal-row2">
            <div>
              <label for="minValue">Slider Min</label>
              <input
                style="width: 99%;"
                v-model="transformation.sliderBounds.min"
                class="dropdown"
                :class="!transformation.sliderBounds.min ? 'is--error': ''"
              />
            </div>
            <div>
              <label for="maxValue">Slider Max</label>
              <input
                style="width: 99%;"
                v-model="transformation.sliderBounds.max"
                class="dropdown"
                :class="!transformation.sliderBounds.max ? 'is--error': ''"
              />
            </div>
          </div>
        </div>
        <div class="" v-else-if="transformation.attribute === 'SYNC-Constant'">
          <label for="latex">Latex</label>
          <input
            v-model="transformation.latex"
            class="dropdown"
            :class="!transformation.latex ? 'is--error': ''"
          />
        </div>
        <div class="normal.row2" v-else-if="transformation.attribute === 'Custom'">
          <div>
            <label for="customAttr">Attribute</label>
            <input
              v-model="transformation.customAttr"
              class="dropdown"
              :class="!transformation.customAttr ? 'is--error': ''"
              type="text"
            />
          </div>
          <div>
            <label for="customTrans">Value</label>
            <input
              v-model="transformation.customTrans"
              class="dropdown"
              :class="!transformation.customTrans ? 'is--error': ''"
              type="text"
            />
          </div>
        </div>
        <div class="normal-row2" v-else-if="transformation.attribute === 'Math Boundary'">
          <div>
            <label for="left">left</label>
            <input
              v-model="transformation.left"
              class="dropdown"
              style="width: 99%;"
              :class="!transformation.left ? 'is--error': ''"
              type="text"
            />
          </div>
          <div>
            <label for="right">right</label>
            <input
              v-model="transformation.right"
              class="dropdown"
              style="width: 99%;"
              :class="!transformation.right ? 'is--error': ''"
              type="text"
            />
          </div>
          <div>
            <label for="bottom">bottom</label>
            <input
              v-model="transformation.bottom"
              class="dropdown"
              style="width: 99%;"
              :class="!transformation.bottom ? 'is--error': ''"
              type="text"
            />
          </div>
          <div>
            <label for="top">top</label>
            <input
              v-model="transformation.top"
              class="dropdown"
              style="width: 99%;"
              :class="!transformation.top ? 'is--error': ''"
              type="text"
            />
          </div>
        </div>
        <div class="" v-else-if="transformation.attribute === 'xAxisLabel'">
          <input
            v-model="transformation.xAxisLabel"
            class="dropdown"
            :class="!transformation.xAxisLabel ? 'is--error': ''"
            type="text"
          />
        </div>
        <div class="" v-else-if="transformation.attribute === 'yAxisLabel'">
          <input
            v-model="transformation.yAxisLabel"
            class="dropdown"
            :class="!transformation.yAxisLabel ? 'is--error': ''"
            type="text"
          />
        </div>
      </div>

      <div class="w-form-done">
        <div>Thank you! Your submission has been received!</div>
      </div>
      <div class="w-form-fail">
        <div>Oops! Something went wrong while submitting the form.</div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      componentIdOption: [],
    };
  },
  props: {
    desmosUrls: {
      type: Array
    },
    editingFrameId: {
      type: String,
    },
    transformation: {
      type: Object,
      defaultValue: {
        desmosId: window.desmosId || 1,
        compId: "",
        attribute: "",
        value: "",
        latex: "",
        attributeOption: [],
        transformOption: [],
        sliderBounds: {
          min: 0,
          max: 15,
          step: .01
        },
        customAttr: "",
        customTrans: "",
      }
    },
    transformationIndex: {
      type: Number,
    }
  },
  components: {
  },
  updated () {
    // this.getCompId(this.transformation.desmosId)
  },
  methods: {
    getCompId(desmosId) {
      desmosId ? desmosId: desmosId = window.desmosId;
      const expressions = window[`calculator${desmosId}`].getExpressions();
      const componentIdOption = [];
      expressions.forEach((exp, idx) => {
        exp.latex = exp.latex ? exp.latex.slice(0, 35) : '';
        if (exp.latex) {
          componentIdOption.push({ ...exp, idx: idx+1 });
        }
      });
      this.componentIdOption = componentIdOption;
    },
    getAttr(option) {
      this.transformation.transformOption = [];
      if (option && option === "Color") {
        this.transformation.transformOption = [
          "green",
          "blue",
          "red",
          "pink",
          "yellow",
          "purple",
          "guideline",
          "white",
        ];
      } else if (option && option === "Hidden") {
        this.transformation.transformOption = [
          "true",
          "false",
        ];
      } else if (option && option === "Select") {
        this.transformation.transformOption = [
          "true",
          "false",
          "default",
        ];
      } else if (option && (option === "showLabel" || option === "pointActive")) {
        this.transformation.transformOption = [
          "true",
          "false",
        ];
      } else {
        return "";
      }
    },
    setAutoStateAndDetectAttribute(id, desmosId) {
      // reset all part of a transformation
      this.transformation.attribute = "";
      this.transformation.latex = "";
      this.transformation.sliderBounds = {};
      this.transformation.attributeOption = [
        "Hidden",
        "Color",
        "Select",
        "Custom",
      ];

      // detect desmos canvas
      if (id === "desmosCanvas") {
        this.transformation.attribute = "Math Boundary";
        this.transformation.attributeOption = [
          "Math Boundary", "xAxisLabel", "yAxisLabel"
        ];
      }
      // auto change latex
      const expressions = window[`calculator${desmosId}`].getExpressions();
      expressions.forEach((item, idx) => {
        // set idx for transformation
        if (this.transformation.compId === item.id) this.transformation.expidx = idx+1;
        if (item.type === "expression" && item.id === id) {
          this.transformation.latex = item.latex;
          const ilatex = item.latex.split("=").pop();
          if (item.domain && !isNaN(ilatex)) {
            this.transformation.attribute = "SYNC";
            this.transformation.attributeOption = [
              "SYNC", "SYNC-Custom", "SYNC-C-Backward", "SYNC-Backward", "SYNC-Constant"
            ];
            this.transformation.sliderBounds = item?.sliderBounds || item?.slider || { min: 0, max: 15, step: .01 }
          }
          const lLatex = item.latex.slice(1, 6);
          if (lLatex === "left(") {
            this.transformation.attributeOption = [
              "Hidden",
              "Color",
              "Select",
              "Custom",
              "showLabel",
              "pointActive"
            ];
          }
        }
      });
    },
  },
};
</script>
<style scoped>
.vertical-center {
  position: sticky;
  top: 50%;
  margin-left: -20px;
  width: 10px;
}
.image-3 {
  position: relative;
  margin-right: 0px;
}
.ta-row2 {
  display: grid;
  grid-template-columns: 92% 7%;
}
</style>
